import {
  Component,
  OnInit,
  QueryList,
  ViewChildren,
  ViewChild,
  ElementRef,
  AfterViewInit,
} from '@angular/core'
import { CommonModule } from '@angular/common'
import { FormsModule, ReactiveFormsModule } from '@angular/forms'
import {
  IonContent,
  IonInput,
  IonButton,
  IonCol,
  IonText,
  IonRow,
  IonItem,
  IonLabel,
  IonIcon,
  LoadingController,
} from '@ionic/angular/standalone'
import { TranslateModule, TranslateService } from '@ngx-translate/core'
import { HeaderComponent } from 'src/app/components/header/header.component'
import { Router } from '@angular/router'
import { ToastService, ToastType } from 'src/app/services/utils/toast.service'
import { addIcons } from 'ionicons'
import { eyeOutline } from 'ionicons/icons'

import { confirmResetPassword } from 'aws-amplify/auth'
import { ErrorHandlerService } from 'src/app/services/utils/error-handler.service'

@Component({
  selector: 'app-confirm-code',
  templateUrl: './confirm-code.page.html',
  styleUrls: ['./confirm-code.page.scss'],
  imports: [
    IonContent,
    IonInput,
    IonCol,
    IonText,
    IonRow,
    IonItem,
    IonButton,
    IonIcon,
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    TranslateModule,
    HeaderComponent,
  ],
})
export class ConfirmCodePage implements OnInit, AfterViewInit {
  @ViewChildren('codeInput') codeInputs!: QueryList<IonInput>
  @ViewChild('otpInput') otpInput!: ElementRef<HTMLInputElement>
  private phone: string = ''
  verificationDigits: string[] = []
  verificationCode: string = ''
  password: string = ''
  confirmPassword: string = ''
  showPassword: boolean = false
  showConfirmPassword: boolean = false
  slots: string[] = new Array(6).fill('')
  activeSlotIndex: number = 0

  constructor(
    private translateService: TranslateService,
    private router: Router,
    private loadingCtrl: LoadingController,
    private toastService: ToastService,
    private errorHandler: ErrorHandlerService
  ) {
    addIcons({ eyeOutline })
    const navigation = this.router.getCurrentNavigation()
    this.phone = navigation?.extras?.state?.['phone'] || this.phone
  }

  ngOnInit() {
    if (!this.phone) {
      this.router.navigate(['/enter-phone'])
    }
  }

  onPaste(event: ClipboardEvent) {
    event.preventDefault()
    const pastedText = event.clipboardData?.getData('text')
    if (pastedText) {
      const digits = pastedText.slice(0, 6).split('')
      digits.forEach((digit, index) => {
        if (index < 6) {
          this.verificationDigits[index] = digit
        }
      })
      this.verificationCode = this.verificationDigits.join('')
    }
  }

  togglePasswordVisibility() {
    this.showPassword = !this.showPassword
  }
  toggleConfirmPasswordVisibility() {
    this.showConfirmPassword = !this.showConfirmPassword
  }

  async confirmCode() {
    const loading = await this.loadingCtrl.create({
      message: this.translateService.instant('generic.please_wait'),
    })
    await loading.present()
    await confirmResetPassword({
      username: this.phone,
      confirmationCode: this.verificationCode,
      newPassword: this.password,
    })
      .then(() => {
        loading.dismiss()
        this.toastService.createWithJustMessage(
          this.translateService.instant('confirm_code.success'),
          ToastType.Success
        )
        this.router.navigate(['/signin'], {
          state: { phone: this.phone },
        })
      })
      .catch((error) => {
        this.errorHandler.handleError(error)
        loading.dismiss()
      })
  }

  onInput(event: Event) {
    const input = event.target as HTMLInputElement
    const value = input.value.replace(/[^0-9]/g, '')

    if (value.length === 6) {
      this.slots = value.split('')
      this.verificationCode = value
      this.activeSlotIndex = 5
      return
    }

    const lastChar = value[value.length - 1]
    if (lastChar) {
      this.slots[this.activeSlotIndex] = lastChar
      if (this.activeSlotIndex < 5) {
        this.activeSlotIndex++
      }
    }

    this.verificationCode = this.slots.join('')
    input.value = this.verificationCode
  }

  onKeyDown(event: KeyboardEvent) {
    switch (event.key) {
      case 'ArrowLeft':
        event.preventDefault()
        this.activeSlotIndex = Math.max(0, this.activeSlotIndex - 1)
        break
      case 'ArrowRight':
        event.preventDefault()
        this.activeSlotIndex = Math.min(5, this.activeSlotIndex + 1)
        break
      case 'Backspace':
        event.preventDefault()
        if (this.slots[this.activeSlotIndex]) {
          this.slots[this.activeSlotIndex] = ''
          this.verificationCode = this.slots.join('')
        } else if (this.activeSlotIndex > 0) {
          this.activeSlotIndex--
          this.slots[this.activeSlotIndex] = ''
          this.verificationCode = this.slots.join('')
        }
        break
      default:
        if (/^\d$/.test(event.key)) {
          event.preventDefault()
          this.slots[this.activeSlotIndex] = event.key
          this.verificationCode = this.slots.join('')
          if (this.activeSlotIndex < 5) {
            this.activeSlotIndex++
          }
        }
    }
    this.otpInput.nativeElement.value = this.verificationCode
    this.setInputCursor(this.activeSlotIndex)
  }

  onSlotClick(index: number) {
    this.activeSlotIndex = index
    this.otpInput.nativeElement.focus()
    this.setInputCursor(index)
  }

  setInputCursor(position: number) {
    requestAnimationFrame(() => {
      this.otpInput.nativeElement.setSelectionRange(position, position)
    })
  }

  ngAfterViewInit() {
    setTimeout(() => {
      this.otpInput.nativeElement.focus()
    }, 0)
  }
}
